.nodeContainer {
  position: fixed;
  padding: var(--spacing-sm);
  border: 1px solid #e2e8f0;
  border-radius: 8px;
  min-width: 300px;
  max-width: 400px;
  height: 100%;

  & & .children {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(2, auto);
    row-gap: var(--spacing-md);
  }
}
